import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';
import './style.css';


const SEARCH = document.getElementById('input');
const BUTTON = document.getElementById('button');
const SPINNER = document.getElementById('spinner');
const NORESULTS = document.getElementById('no-results');
const CAROUSEL_INNER = document.querySelector('.carousel-inner');
const BREAKPOINTS = document.documentElement.clientWidth;
const INDICATORS = document.querySelector('.carousel-indicators');
const RIGHT_ARROW = document.querySelector('.carousel-control-next');
// const LEFT_ARROW = document.querySelector('.carousel-control-prev');
let word = 'what';
let pageCount = 2;
let slidesNumber = 0;

function listCreation(slidesNumber) {
  for (let i = 0; i < slidesNumber; i++) {
    const ul = document.createElement('ul');
    ul.classList.add('carousel-item', 'list-inline', `ul${i}`);
    if (i === 0) {
      ul.classList.add('active', 'first');
    } else if (i === slidesNumber - 1) {
      ul.classList.add('last');
    }
    CAROUSEL_INNER.append(ul);
  }
}

function carouselIndicators(slidesNumber) {
  for (let i = 0; i < slidesNumber; i++) {
    const li = document.createElement('li');
    li.setAttribute('data-target', '#featured');
    li.setAttribute('data-slide-to', `${i}`);
    li.classList.add('carousel-indicator');
    INDICATORS.append(li);
  }
}

function createSlider(data) {
  if (BREAKPOINTS > 992) {
    slidesNumber = 3;
  } else if (BREAKPOINTS > 576) {
    slidesNumber = 5;
  } else if (BREAKPOINTS > 319) {
    slidesNumber = 10;
  }

  listCreation(slidesNumber);
  carouselIndicators(slidesNumber);

  for (let i = 0; i < data.Search.length; i++) {
    const li = document.createElement('li');
    li.classList.add('list-inline-item');
    const a = document.createElement('a');
    a.href = `https://www.imdb.com/title/${data.Search[i].imdbID}/videogallery/?ref_=tt_pv_vi_sm`;
    const h2 = document.createElement('h2');
    const div = document.createElement('div');
    div.classList.add('image-container');
    const img = document.createElement('img');
    const p = document.createElement('p');
    h2.append(data.Search[i].Title);
    img.src = data.Search[i].Poster;
    p.append(data.Search[i].Year);
    p.classList.add('year');

    const rating = document.createElement('p');
    rating.classList.add(`${data.Search[i].imdbID}`);
    rating.classList.add('rating');
    getMovieRaiting(data, i);


    a.append(h2);
    div.append(img);
    img.onload = function () {
      img.style.opacity = 1;
    };
    li.append(a);
    li.append(div);
    li.append(p);
    li.append(rating);


    if (BREAKPOINTS > 992) {
      switch (true) {
        case i < 4: document.querySelector('.ul0').append(li);
          break;
        case i < 8: document.querySelector('.ul1').append(li);
          break;
        default: document.querySelector('.ul2').append(li);
      }
    } else if (BREAKPOINTS > 576) {
      switch (true) {
        case i < 2: document.querySelector('.ul0').append(li);
          break;
        case i < 4: document.querySelector('.ul1').append(li);
          break;
        case i < 6: document.querySelector('.ul2').append(li);
          break;
        case i < 8: document.querySelector('.ul3').append(li);
          break;
        default: document.querySelector('.ul4').append(li);
      }
    } else if (BREAKPOINTS > 319) {
      document.querySelector(`.ul${i}`).append(li);
    }
  }
}

function createRating(data, i, data2) {
  document.querySelector(`.${data.Search[i].imdbID}`).append(data2.imdbRating);
}


window.onload = function () {
  SEARCH.focus();
  getMovieInfo(word, 1);
};


BUTTON.addEventListener('click', () => {
  event.preventDefault();
  for (const node of document.querySelectorAll('.carousel-item')) {
    node.remove();
  }
  for (const node of document.querySelectorAll('.carousel-indicator')) {
    node.remove();
  }
  NORESULTS.innerText = '';
  SPINNER.style.display = 'inline-block';
  setTimeout(() => {
    SPINNER.style.display = 'none';
  }, 500);

  word = SEARCH.value.toString();
  getMovieInfo(word, 1);
});


RIGHT_ARROW.addEventListener('click', () => {
  if (document.querySelector('.last').classList.contains('active')) {
    for (const node of document.querySelectorAll('.carousel-item')) {
      node.remove();
    }
    for (const node of document.querySelectorAll('.carousel-indicator')) {
      node.remove();
    }
    getMovieInfo(word, pageCount);
    pageCount++;
  }
});


async function getMovieInfo(word, page) {
  const url = `https://www.omdbapi.com/?s==${word}&page=${page}&apikey=8510378b`;

  const res = await fetch(url);
  const data = await res.json();

  try {
    const res = await fetch(url);
    const data = await res.json();
  } catch (err) {
    console.log(err);
  }

  if (data.Response === 'False') {
    getMovieInfo('what');
    NORESULTS.innerText = `No results for '${word}'`;
  } else {
    createSlider(data);
  }
  return data;
}

async function getMovieRaiting(data, i) {
  const url = `https://www.omdbapi.com/?i=${data.Search[i].imdbID}&apikey=8510378b`;

  const res = await fetch(url);
  const data2 = await res.json();

  createRating(data, i, data2);

  return data2;
}
